html {
  background: url("background.7f610dc1.jpg") bottom / cover;
  font-size: 10px;
}

body, html {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.keys {
  min-height: 100vh;
  flex: 1;
  justify-content: center;
  align-items: center;
  display: flex;
}

.key {
  width: 10rem;
  text-align: center;
  color: #fff;
  text-shadow: 0 0 .5rem #000;
  background: rgba(0, 0, 0, .4);
  border: .4rem solid #000;
  border-radius: .5rem;
  margin: 1rem;
  padding: 1rem .5rem;
  font-size: 1.5rem;
  transition: all 70ms;
}

.playing {
  border-color: #ffc600;
  transform: scale(1.1);
  box-shadow: 0 0 1rem #ffc600;
}

kbd {
  font-size: 4rem;
  display: block;
}

.sound {
  text-transform: uppercase;
  letter-spacing: .1rem;
  color: #ffc600;
  font-size: 1.2rem;
}

